.lists-overview {
	grid-area: lists;
}

.lists-overview ul {
  font-size: 1.2rem;
  line-height: 1.7;
  list-style: circle;
  padding-left: 1.1em;
}

.lists-overview li {
  cursor: pointer;
}

.lists-overview li:hover {
  opacity: 0.7;
}

.lists-overview li.active {
  font-weight: 700;
}

.lists-overview .add-input input {
	font-size: 1.1rem;
}
